.useful {
  position: relative;

  @media(min-width: 1024px) {
    margin-top: 150px;
  }

  &__title {
    margin-top: -50px;
  }

  &__number {
    color: #fff;
  }

  &__subtitle {}

  &__description {
    margin-top: 0;
    margin-bottom: 70px;
    font-weight: 400;
    font-size: 16px;

    @media(min-width: 768px) {
      font-size: 18px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-top: 0;
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;

    @media(min-width: 768px) {
      flex-direction: row;
    }
  }

  &__item {
    margin-right: 0;
    padding: 50px 25px;
    border: 2px solid #49b462;
    border-radius: 20px;

    @media(min-width: 768px) {
      margin-right: 30px;
    }

    @media(min-width: 1024px) {
      width: calc(100% / 3);
      max-width: 380px;
      max-height: 217px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 300;
    font-size: 16px;
  }

  &__button {}
}
