.numbers {

  &__list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    padding: 50px 0;
    list-style: none;
    background-image: linear-gradient(to right, #3bd45f 0%, #49b462 100%);
    border-radius: 20px;
    box-shadow: 0 10px 30px rgba(55, 124, 78, 0.1);

    @media(min-width: 1024px) {
      flex-direction: row;
      justify-content: space-around;
      padding: 70px 0;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 30px;
    margin-bottom: 30px;
    margin-left: 30px;
    text-align: center;

    @media(min-width: 768px) {
      flex-direction: row;
      flex-wrap: nowrap;
      width: 320px;
      text-align: left;
    }


    @media(min-width: 1024px) {
      width: auto;
      max-width: 225px;
      margin-bottom: 0;
    }
  }
}

.number {

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 50px;
    color: #fff;
  }

  &__text {
    margin: 0;
    margin-left: 15px;
    font-weight: 400;
    font-size: 18px;
    color: #fff;
  }
}
