.price {
  position: relative;

  &__title {
    margin-bottom: 85px;
  }

  &__wrapper-table {}

  &__table {
    display: none;
    width: 100%;
    text-align: center;
    border-radius: 20px;

    &--active {
      display: block;
    }
  }

  &__unvisible {
    font-size: 0;
    color: transparent;

    @media(min-width:768px) {
      font-size: 18px;
    }
  }

  .table {

    &__title {
      position: relative;
      margin-top: 30px;
      margin-bottom: 35px;
      font-weight: 700;
      font-size: 24px;
      color: #183119;
      cursor: pointer;
    }

    &__arrow {
      position: absolute;
      top: 0;
      right: 0;
      transition: 0.3s;

      &--green {

        transform: rotate(180deg);

        path {
          fill: #49b462;
        }
      }
    }

    &__header {
      font-weight: 700;
      font-size: 8px;
      text-align: center;

      @media(min-width: 768px) {
        font-size: 12px;
      }
    }

    &__head {
      padding: 10px 0;

      &--1 {
        width: 33%;
        border-radius: 20px 0px 0px 0px;
      }

      &--2 {
        width: 33%;
      }

      &--3 {
        width: 33%;
        border-radius: 0px 20px 0px 0px;
      }
    }

    &__content {
      padding: 20px;
      font-weight: 400;
      font-size: 8px;

      @media(min-width: 768px) {
        font-size: 18px;
      }
    }

    &__part {
      &--brown {
        background-color: #fcf2d7;

        &-light {
          background-color: #fdfaf3;
        }
      }

      &--b-l {
        border-radius: 0px 0px 0px 20px;
      }

      &--b-c {}

      &--b-r {
        border-radius: 0px 0px 20px 0px;
      }
    }

    &__left {
      text-align: left;
    }
  }

  &__button {
    margin-top: 65px;

    @media(min-width: 768px) {
      margin-top: 150px;
    }
  }
}
